* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f7f7f7;
  font-family: Georgia, serif;
}

.header-container {
  height: 200px;
  position: relative;
}

header {
  text-align: center;
  color: #000;
  padding: 40px 20px;
  font-family: Ewert, system-ui;
  font-style: normal;
  font-weight: 400;
}

nav {
  z-index: 2;
  background-color: #ffffffe6;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
  position: relative;
}

nav ul {
  gap: 15px;
  list-style: none;
  display: flex;
}

nav ul li a {
  color: #007bff;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav ul li a:hover {
  color: #0056b3;
}

.burger {
  cursor: pointer;
  flex-direction: column;
  justify-content: space-between;
  width: 25px;
  height: 20px;
  margin-left: auto;
  display: none;
}

.burger span {
  background-color: #333;
  border-radius: 2px;
  width: 100%;
  height: 3px;
}

@media (width <= 768px) {
  nav ul {
    text-align: center;
    flex-direction: column;
    width: 100%;
    display: none;
  }

  .burger {
    margin-top: 10px;
    display: flex;
  }

  nav ul.show {
    display: flex;
  }

  nav ul li {
    padding: 10px 0;
  }

  nav ul li a {
    color: #333;
    text-transform: uppercase;
    font-weight: bold;
    text-decoration: none;
  }

  nav {
    background-color: #ffffffe6;
    padding: 10px;
    box-shadow: 0 4px 8px #0000001a;
  }

  .news-layout {
    flex-direction: column;
  }

  #opinion-sidebar {
    margin-top: 20px;
    margin-left: 0;
  }
}

.carousel {
  background-color: #f1f1f1;
  width: 100%;
  height: 400px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.carousel img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: opacity 1s ease-in-out;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.news-layout {
  padding: 20px;
  display: flex;
}

.news-grid {
  flex: 3;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.news-card {
  background-color: #fff;
  border-radius: 5px;
  transition: box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 2px 5px #0000001a;
}

.news-card:hover {
  box-shadow: 0 4px 10px #0003;
}

.news-card img {
  width: 100%;
  height: auto;
}

.news-card h3 {
  color: #333;
  margin: 10px;
  font-size: 1.2rem;
}

.news-card p {
  color: #666;
  margin: 0 10px 10px;
  font-size: .9rem;
}

#opinion-sidebar {
  background-color: #f9f9f9;
  border-radius: 5px;
  flex: 1;
  margin-left: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

#opinion-sidebar h2 {
  margin-bottom: 15px;
  font-size: 1.5rem;
}

.opinion-item {
  margin-bottom: 15px;
}

footer {
  text-align: center;
  background-color: #f1f1f1;
  margin-top: 20px;
  padding: 20px;
  font-size: .9rem;
}

footer p {
  color: #666;
  margin: 5px 0;
}
